import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Button,
} from "@material-ui/core";
import React from "react";

interface Props {
  isOpen: boolean;
  handleClose: () => void;
  goToLogInPage: () => void;
  age?: null | number;
}

export const EnterGenderAndAgeDialog = ({
  isOpen,
  handleClose,
  goToLogInPage,
  age,
}: Props) => {
  return (
    <Dialog open={isOpen} onClose={handleClose}>
      <DialogTitle></DialogTitle>
      <DialogContent>
        <DialogContentText>
          
          {age !== undefined && age !== null && age < 16
            ? "16"
            : null}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={goToLogInPage} color="primary" autoFocus>
          
        </Button>
      </DialogActions>
    </Dialog>
  );
};
